// Simple test to verify JWT mocking works
const mockJwt = {
  verify: jest.fn(),
  sign: jest.fn(),
};

jest.mock('jsonwebtoken', () => mockJwt);

import jwt from 'jsonwebtoken';

describe('JWT Mock Test', () => {
  test('should use mocked jwt.verify', () => {
    mockJwt.verify.mockReturnValue({ sub: 1, username: 'test' });
    
    const result = jwt.verify('test-token', 'secret');
    
    expect(result).toEqual({ sub: 1, username: 'test' });
    expect(mockJwt.verify).toHaveBeenCalledWith('test-token', 'secret');
  });
});